import { Aperture, CircleAperture, RectangleAperture, RegularPolygonAperture, RegionAperture } from './aperture'
import { Operation, LinearOperation, TranslateOperation, RegionOperation, ArcOperation } from './operation'

enum Unit {
    Millimeter,
    Inch
}

enum DrawMode {
    Linear,
    Arc
}

class NotImplementError extends Error{

}

class ParameterError extends Error{

}

export class G274Parser {
    _drawmode = DrawMode.Linear;
    _clockwise = false;
    _clearstate: boolean = false;
    _currentpos: [number, number] = [0, 0];
    _regionstate: boolean = false;
    _multiquadrant: boolean = true;
    _decimallen: number = 0;
    _integerlen: number = 0;
    _unit: Unit = Unit.Millimeter;

    _aperture_map = new Map<number, Aperture>();
    _current_aperture!: Aperture;

    _operations: Operation[] = [];
    _factor: number = 1;
    _region_aperture: RegionAperture = new RegionAperture();

    static _re_string: RegExp = /[a-zA-Z0-9_+-/!?<>”’(){}.\\|&@# ,;$:=]+/;
    static _re_name: RegExp = /[a-zA-Z_.$][a-zA-Z_.0-9]*/;
    static _re_hex: RegExp = /[a-fA-F0-9]+/;
    static _re_decimal: RegExp = / /;
    static _re_Integer: RegExp = / /;
    // End
    static _re_M02 = /^M02$/;
    static _re_FS = /FS(LA)?X([0-9])([0-9])Y([0-9])([0-9])/;
    static _re_MM = /MO(IN|MM)/;
    static _re_D01 = /(X([+0-9-]+))?(Y([+0-9-]+))?(I([+0-9-]+))?(J([+0-9-]+))?D01/;
    static _re_D02 = /(X([+0-9-]+))?(Y([+0-9-]+))?D02/;
    static _re_D03 = /(X([+0-9-]+))?(Y([+0-9-]+))?D03/;
    static _re_Dnn = /^D(\d+)/;
    static _re_G01 = /^G01$/;
    // Clockwise
    static _re_G02 = /^G02$/;
    // CounterClockwise
    static _re_G03 = /^G03$/;
    // Commeng
    static _re_G04 = new RegExp(/^G04/.source + G274Parser._re_string.source);
    // Region On
    static _re_G36 = /^G36$/;
    // Region Off
    static _re_G37 = /^G37$/;
    // deprecated
    // select aperture
    static _re_G54 = /^G54D([0-9]+)/;
    // Single Quadrant
    static _re_G74 = /^G74$/;
    // Multi Quadrant
    static _re_G75 = /^G75$/;

    static _re_LP = /^LP(C|D)$/

    static _re_AD = new RegExp(/^ADD([0-9]+)/.source + '(' + G274Parser._re_name.source + ')(.*)');

    // string current position
    _current = 0;

    generateOperation(coord: number[]){
        if(coord.length == 2){
            if(this._regionstate){
                this._region_aperture.Points.push([coord[0], coord[1], false, false]);
            }else{
                let op = new LinearOperation(this._current_aperture, this._currentpos, [coord[0], coord[1]], this._clearstate);
                this._operations.push(op);
            }
            this._currentpos= [coord[0], coord[1]];
        }else if(coord.length == 4){
            if(this._multiquadrant){
                if(this._regionstate){
                    if(this._region_aperture.Points.length == 0){
                        this._region_aperture.Points.push([this._currentpos[0], this._currentpos[1], false, false]);
                        this._region_aperture.Points.push([this._currentpos[0] + coord[2], this._currentpos[1] + coord[3], true, this._clockwise]);
                        this._region_aperture.Points.push([coord[0], coord[1], false, false]);
                    }else{
                        this._region_aperture.Points.push([this._currentpos[0] + coord[2], this._currentpos[1] + coord[3], true, this._clockwise]);
                        this._region_aperture.Points.push([coord[0], coord[1], false, false]);
                    }
                }else{
                    let op = new ArcOperation(this._current_aperture, this._currentpos, [coord[0], coord[1]],
                        [this._currentpos[0] + coord[2], this._currentpos[1] + coord[3]], this._clockwise, this._clearstate);
                    this._operations.push(op);
                }
                this._currentpos= [coord[0], coord[1]];
            }else{
                throw new NotImplementError();
            }
        }else{
            throw new ParameterError();
        }
    }

    parserG01(content: string): boolean{
        let res = G274Parser._re_G01.exec(content);
        if (res) {
            this._drawmode = DrawMode.Linear;
            return true;
        }
        let deprecatedG01 = /^G01(.*)/;
        res = deprecatedG01.exec(content);
        if (res) {
            // console.log('Deprecated G01 Usage');
            this._drawmode = DrawMode.Linear;
            if (this._regionstate) {
                let coord = this.parserCoordinate(res[1]);
                if (/D02^/.exec(res[1])) {
                    this._currentpos = [coord[0], coord[1]];
                } else {
                    this.generateOperation([coord[0], coord[1]]);
                }
            } else {
                throw '';
            }
            return true;
        }
        return false;
    }

    parserG02(content: string): boolean{
        let res = G274Parser._re_G02.exec(content);
        if (res) {
            this._drawmode = DrawMode.Arc;
            this._clockwise = true;
            return true;
        }
        let deprecatedG02 = /^G02(.*)/;
        res = deprecatedG02.exec(content);
        if (res) {
            this._clockwise = true;
            // console.log('Deprecated G02 Usage');
            let coord = this.parserCoordinate(res[1]);
            this.generateOperation([coord[0], coord[1], coord[2], coord[3]]);
            return true;
        }
        return false;
    }

    parserG03(content: string): boolean{
        let res = G274Parser._re_G03.exec(content);
        if (res) {
            this._drawmode = DrawMode.Arc;
            this._clockwise = false;
            return true;
        }
        let deprecatedG03 = /^G03(.*)/;
        res = deprecatedG03.exec(content);
        if (res) {
            this._clockwise = false;
            // console.log('Deprecated G03 Usage');
            let coord = this.parserCoordinate(res[1]);
            this.generateOperation([coord[0], coord[1], coord[2], coord[3]]);
            return true;
        }
        return false;
    }

    parserCoordinate(content: string){
        let coord = /^(X([+0-9-]+))?(Y([+0-9-]+))?(I([+0-9-]+))?(J([+0-9-]+))?/;
        let res = coord.exec(content);
        let coordres:number[] = [];
        if(res){
            if(res[2]){
                coordres.push(Number.parseInt(res[2]) / this._factor);
            }else{
                coordres.push(this._currentpos[0]);
            }
            if(res[4]){
                coordres.push(Number.parseInt(res[4]) / this._factor);
            }else{
                coordres.push(this._currentpos[1]);
            }
            if(res[6]){
                coordres.push(Number.parseInt(res[6]) / this._factor);
            }else{
                coordres.push(0);
            }
            if(res[8]){
                coordres.push(Number.parseInt(res[8]) / this._factor);
            }else{
                coordres.push(0);
            }
        }
        return coordres;
    }
    
    parserD01(content: string): boolean{
        let res = G274Parser._re_D01.exec(content);
        if(!res){
            let deprecatedD01 = /^(X([+0-9-]+))?(Y([+0-9-]+))?(I([+0-9-]+))?(J([+0-9-]+))?$/;
            res = deprecatedD01.exec(content);
            // if(res) console.log('Deprecated D01 Usage');
        }
        if (res) {
            let x: number;
            let y: number;
            if (res[2]) {
                x = Number.parseInt(res[2]) / this._factor;
            } else {
                x = this._currentpos[0];
            }
            if (res[4]) {
                y = Number.parseInt(res[4]) / this._factor;
            } else {
                y = this._currentpos[1];
            }
            if (this._drawmode == DrawMode.Linear) {
                this.generateOperation([x, y]);
            } else if (this._drawmode == DrawMode.Arc) {
                this._drawmode = DrawMode.Linear;
                let dx = 0;
                let dy = 0;
                if(res[6]){
                    dx = Number.parseInt(res[6])/ this._factor;
                }
                if(res[8]){
                    dy = Number.parseInt(res[8]) / this._factor;
                }
                this.generateOperation([x, y, dx, dy]);
            }
            this._drawmode = DrawMode.Linear;
            this._currentpos = [x, y];
            return true;
        }
        return false;
    }

    parserD02(content: string): boolean{
        let res = G274Parser._re_D02.exec(content);
        if (res) {
            this._currentpos = [this._currentpos[0], this._currentpos[1]];
            if (res[2]) {
                this._currentpos[0] = Number.parseInt(res[2]) / this._factor;
            }
            if (res[4]) {
                this._currentpos[1] = Number.parseInt(res[4]) / this._factor;
            }
            if (this._regionstate) {
                if (this._region_aperture.Points.length > 2) {
                    this._operations.push(new RegionOperation(this._region_aperture, this._clearstate));
                }
                this._region_aperture = new RegionAperture();
            }
            return true;
        }
        return false;
    }

    parserAD(content: string): boolean{
        let res = G274Parser._re_AD.exec(content);
        if (res) {
            let id = Number.parseInt(res[1]);
            if (id < 10) {
                throw 'Aperture ID Must Larger Than 10';
            }
            switch (res[2]) {
                case 'C': {
                    let re_circle = /,([-.0-9]+)(X([-.0-9]+))?/;
                    let subres = re_circle.exec(res[3]);
                    if (subres) {
                        let holed = 0;
                        if (subres[3]) {
                            holed = Number.parseFloat(subres[3]);
                        }
                        let dia = Number.parseFloat(subres[1]);
                        let aperture = new CircleAperture(dia, holed);
                        this._aperture_map.set(Number.parseInt(res[1]), aperture);
                    } else {
                        throw new Error('Circle Aperture Format Error');
                    }
                    break;
                }
                case 'R': {
                    let re_rect = /,([-.0-9]+)X([-.0-9]+)(X([-.0-9]+))?/;
                    let subres = re_rect.exec(res[3]);
                    if (subres) {
                        let width = Number.parseFloat(subres[1]);
                        let height = Number.parseFloat(subres[2]);
                        let holed = 0;
                        if (subres[4]) {
                            holed = Number.parseFloat(subres[4]);
                        }
                        let aperture = new RectangleAperture(width, height, holed);
                        this._aperture_map.set(Number.parseInt(res[1]), aperture);
                    } else {
                        throw new Error('Rectangle Aperture Format Error');
                    }
                    break;
                }
                case 'O': {
                    let re_obround = /,([-.0-9]+)X([-.0-9]+)(X([-.0-9]+))?/;
                    let subres = re_obround.exec(res[3]);
                    if (subres) {
                        let width = Number.parseFloat(subres[1]);
                        let height = Number.parseFloat(subres[2]);
                        let holed = 0;
                        if (subres[4]) {
                            holed = Number.parseFloat(subres[4]);
                        }
                        let aperture = new RectangleAperture(width, height, holed);
                        this._aperture_map.set(Number.parseInt(res[1]), aperture);
                    } else {
                        throw new Error('Obround Aperture Format Error');
                    }
                    break;
                }
                case 'P': {
                    let re_polygon = /,([-.0-9]+)X([-.0-9]+)(X([-.0-9]+))?(X([-.0-9]+))?/
                    let subres = re_polygon.exec(res[3]);
                    if (subres) {
                        let diameter = Number.parseFloat(subres[1]);
                        let vertices = Number.parseInt(subres[2]);
                        let rotation = 0;
                        let holed = 0;
                        if (subres[4]) {
                            rotation = Number.parseFloat(subres[4]);
                            if (subres[6]) {
                                holed = Number.parseFloat(subres[6]);
                            }
                        }
                        let aperture = new RegularPolygonAperture(diameter, vertices, rotation, holed);
                        this._aperture_map.set(Number.parseInt(res[1]), aperture);
                    } else {
                        throw new Error('Regular Polygon Aperture Format Error');
                    }
                }
                default:
                    throw new Error('Not Implement');
                    break;
            }
            return true;
        }
        return false;
    }

    public parser(content: string) {
        this._current = 0;
        let re_command = /%?([A-Z][^\*]*)\*%?/;
        while (this._current < content.length) {
            let re_useless = /^[ \t\r\n]*/;
            let restcontent = content.substr(this._current);
            let res = re_useless.exec(restcontent);
            if (res) {
                this._current += res[0].length;
                if (this._current >= content.length)
                    return;
                restcontent = content.substr(this._current);
            }
            res = re_command.exec(restcontent)
            if (res) {
                this._current += res[0].length;
                let cmdstr = res[1];

                res = G274Parser._re_M02.exec(cmdstr);
                if (res) {
                    break;
                }

                res = G274Parser._re_FS.exec(cmdstr);
                if (res) {
                    if (res[3] != res[5] ||
                        res[2] != res[4]) {
                        throw new Error('X Y Format Not Equal');
                    }
                    this._integerlen = Number.parseInt(res[2]);
                    this._decimallen = Number.parseInt(res[3]);
                    this._factor = Math.pow(10, this._decimallen);
                    continue;
                }

                if(this.parserG01(cmdstr)){
                    continue;
                }

                if(this.parserG02(cmdstr)){
                    continue;
                }

                if(this.parserG03(cmdstr)){
                    continue;
                }

                res = G274Parser._re_G03.exec(cmdstr);
                if (res) {
                    this._drawmode = DrawMode.Arc;
                    this._clockwise = false;
                    continue;
                }

                res = G274Parser._re_G04.exec(cmdstr);
                if (res) {
                    continue;
                }

                res = G274Parser._re_G36.exec(cmdstr);
                if (res) {
                    this._region_aperture.clear();
                    this._regionstate = true;
                    continue;
                }

                res = G274Parser._re_G37.exec(cmdstr);
                if (res) {
                    if (this._region_aperture.Points.length > 2) {
                        this._operations.push(new RegionOperation(this._region_aperture, this._clearstate));
                    }
                    this._region_aperture = new RegionAperture();
                    this._regionstate = false;
                    continue;
                }

                res = G274Parser._re_G74.exec(cmdstr);
                if (res) {
                    this._multiquadrant = false;
                    continue;
                }

                res = G274Parser._re_G75.exec(cmdstr);
                if (res) {
                    this._multiquadrant = true;
                    continue;
                }


                if(this.parserD01(cmdstr)){
                    continue;
                }

                if(this.parserD02(cmdstr)){
                    continue;
                }

                res = G274Parser._re_D03.exec(cmdstr);
                if (res) {
                    if (this._regionstate) {
                        throw 'D03 Should Not In Region';
                    }
                    let op = new TranslateOperation(this._current_aperture, [Number.parseInt(res[2]) / this._factor, Number.parseInt(res[4]) / this._factor], this._clearstate);
                    this._operations.push(op);
                    continue;
                }

                res = G274Parser._re_Dnn.exec(cmdstr);
                if (res) {
                    let id = Number.parseInt(res[1]);
                    if (id < 10) {
                        throw 'Select Error Aperture ID';
                    }
                    this._current_aperture = this._aperture_map.get(id)!;
                    continue;
                }

                res = G274Parser._re_G54.exec(cmdstr);
                if (res) {
                    // console.log('G54 is Deprecated');
                    let id = Number.parseInt(res[1]);
                    if (id < 10) {
                        throw 'Select Error Aperture ID';
                    }
                    this._current_aperture = this._aperture_map.get(id)!;
                    continue;
                }

                res = G274Parser._re_LP.exec(cmdstr);
                if (res) {
                    if (res[1] == 'C') {
                        this._clearstate = true;
                    } else {
                        this._clearstate = false;
                    }
                    continue;
                }

                if(this.parserAD(cmdstr)){
                    continue;
                }

            } else {
                throw new Error('UnRecognized Command ' + restcontent);
            }
        }
        console.log('end');
    }
}