import Vue from 'vue'
import VueRouter from 'vue-router'
import Home from '../views/Home.vue'

Vue.use(VueRouter)

const routes = [
  {
    path: '/',
    name: 'home',
    component: Home
  },
  {
    path: '/tensorflow/example1',
    component: () => import('../views/Example1.vue')
  },
  {
    path: '/tensorflow/example2',
    component: () => import('../views/Example2.vue')
  },
  {
    path: '/Stencil',
    component:()=> import('../views/Stencil.vue')
  },
  {
    path: '/tune',
    component: ()=> import('../views/Tune.vue')
  },
  {
    path: '/gerberview',
    component: ()=> import('../views/GerberView.vue')
  }
]

const router = new VueRouter({
  mode: 'history',
  base: process.env.BASE_URL,
  routes
})

export default router
