









import Vue from "vue";
import { G274Parser } from "../parser";
import { Graphic } from '../graphic'
export default Vue.extend({
  data(){
    return {
      graphic: Graphic
    }
  },
  mounted(){
    let btn = <HTMLButtonElement>document.getElementById('confirm');

    let graphic = new Graphic('show');

    btn.onclick = ()=>{
        let file = <HTMLInputElement>document.getElementById('upload');
        if(file.files.length){
            let reader = new FileReader();
            reader.readAsText(file.files[0]);
            reader.onload = (evt)=>{
              let content = reader.result.toString();
              let parser = new G274Parser();
              parser.parser(content!);
              graphic.operationSet = parser._operations;
            };
        }

    }

    this.graphic = graphic;
  },
  destroyed(){
    this.graphic.stop();
  }
});
