export class Aperture{
    protected _name: string = '';
    get Name(){
        return this._name;
    }
}

export class StandardAperture extends Aperture{
    protected _holediameter = 0;
    get HoleDiameter(){
        return this._holediameter;
    }
}

export class CircleAperture extends StandardAperture{
    private _diameter: number = 0;
    get Diameter(){
        return this._diameter;
    }
    constructor(diameter:number, holediameter:number = 0){
        super();
        this._name = 'C';
        this._diameter = diameter;
        this._holediameter = holediameter;
    }
}

export class RectangleAperture extends StandardAperture{
    private _width: number = 0;
    private _height: number = 0;
    get Width(){
        return this._width;
    }
    get Height(){
        return this._height;
    }
    constructor(width: number, height: number, holediameter: number = 0){
        super();
        this._name = 'R';
        this._width = width;
        this._height = height;
        if(holediameter > Math.min(width, height)){
            throw Error('Hole Size Larger Than Rectangle');
        }
        this._holediameter= holediameter;
    }
}

export class ObroundAperture extends StandardAperture{
    private _width = 0;
    private _height = 0;
    get Width(){
        return this._width;
    }
    get Height(){
        return this._height;
    }
    constructor(width: number, height: number, holediameter: number = 0){
        super();
        this._name = 'O';
        this._width = width;
        this._height = height;
        if(holediameter > Math.min(width, height)){
            throw Error('Hole Size Larger Than Rectangle');
        }
        this._holediameter= holediameter;
    }
}

export class RegularPolygonAperture extends StandardAperture{
    private _diameter = 0;
    private _vertices_num = 0;
    // degree
    // counterclockwise > 0
    private _rotation = 0;
    get Diameter(){
        return this._diameter;
    }
    get Count(){
        return this._vertices_num;
    }
    get Rotation(){
        return this._rotation;
    }
    constructor(diamter: number, verticesnum: number, rotation: number, holediameter: number){
        super();
        if(diamter <= 0 ||
            verticesnum < 3 ||
            verticesnum > 12){
                throw Error('REgularPolygon Parameter Error');
            }
        this._diameter = diamter;
        this._vertices_num = verticesnum;
        this._rotation = rotation;
        this._holediameter =holediameter;
    }
}

export class RegionAperture extends Aperture{
    // x, y, iscenter, isclockwise
    private _points: [number, number, boolean, boolean][] = [];
    get Points(){
        return this._points;
    }
    constructor(){
        super();
    }
    pushBack(point: [number, number, boolean, boolean]){
        this._points.push(point);
    }
    clear(){
        this._points = [];
    }
}