


























import Vue from "vue";
import * as tf from "@tensorflow/tfjs";
import DigitInput from '../components/DigitInput.vue';
export default Vue.extend({
  data() {
    return {
      model: <tf.LayersModel | null>null,
      info: "",
      digit: "",
      modelfile: <File | null>null,
      weightfile: <File | null>null
    };
  },
  methods: {
    uploadmodel(file) {
      this.info = "";
      this.modelfile = file.raw;
      if (this.modelfile && this.weightfile) {
        this.upload();
      }
    },
    uploadweight(file) {
      this.info = "";
      this.weightfile = file.raw;
      if (this.modelfile && this.weightfile) {
        this.upload();
      }
    },
    upload() {
      tf.loadLayersModel(tf.io.browserFiles([this.modelfile, this.weightfile]))
        .then((model: tf.LayersModel) => {
          this.model = model;
          model.summary(undefined, undefined, msg => {
            this.info = this.info + msg + "\n";
          });
          this.$message("Load Model Success");
        })
        .catch(reason => {
          this.model = null;
          this.$message.error("Load Model Failed");
        });
    }
  },
  components:{
    DigitInput
  }
});
