
export class Tune {
    audioCtx: AudioContext;
    analyse_node: AnalyserNode;
    constructor(pStream: MediaStream) {
        let audioCtx = new AudioContext();
        this.audioCtx = audioCtx;
        let n1 = this.audioCtx.createMediaStreamSource(pStream);
        let n2 = this.audioCtx.createAnalyser();
        n2.fftSize = Math.pow(2, 15);
        n1.connect(n2);
        this.analyse_node = n2;
    }
    getFrequence(): number {
        let farray = new Uint8Array(this.analyse_node.frequencyBinCount);
        this.analyse_node.getByteFrequencyData(farray);
        let maxindex = 0;
        farray.forEach((value, index) => {
            if (value > farray[maxindex]) {
                maxindex = index;
            }
        });
        if (farray[maxindex] < 100) {
            return null;
        }
        return maxindex * this.audioCtx.sampleRate / this.analyse_node.fftSize
    }
    getName(): [string, number] {
        let fre = this.getFrequence();
        let dis = 999;
        if (fre) {
            while(fre < 261.63){
                fre = fre * 2.0;
            }
            while(fre > 523.25){
                fre = fre * 0.5;
            }
            let rel = new Map<number, string>();
            rel.set(261.63, 'C');
            rel.set(277.18, 'C#');
            rel.set(293.66, 'D');
            rel.set(311.13, 'D#');
            rel.set(329.63, 'E');
            rel.set(349.23, 'F');
            rel.set(369.99, 'F#');
            rel.set(392.00, 'G');
            rel.set(415.30, 'G#');
            rel.set(440.00, 'A');
            rel.set(466.16, 'A#');
            rel.set(493.88, 'B');
            rel.set(523.25, 'C');
            let name: string = ""
            let left = rel.keys()[0];
            let right = rel.keys()[1];
            let iter = rel.entries();
            let res: IteratorResult<[number, string]>;
            while(res = iter.next(), !res.done){
                if(fre > res.value[0]){
                    left = res.value[0];
                }
                if(fre < res.value[0]){
                    right = res.value[0];
                    break;
                }
            }
            let center = (right + left)*0.5;
            if(fre > center){
                name = rel.get(right);
                dis = (fre - right) / ((right - left) * 0.5);
            }else{
                name = rel.get(left);
                dis = (fre - left) / ((right - left) * 0.5);
            }
            return [name, dis];
        }
        return null;
    }
}
