



import Vue from "vue";
import { Matrix3, WebGLRenderer, PerspectiveCamera, Scene, BoxGeometry, CylinderGeometry, MeshLambertMaterial, Color, AmbientLight, Mesh, BackSide, OrthographicCamera, DirectionalLight, MeshPhongMaterial } from "three";
import { OrbitControls } from 'three/examples/jsm/controls/OrbitControls'
export default Vue.extend({
  data() {
    return {
      renderer: WebGLRenderer,
      camera: PerspectiveCamera,
      scene: Scene,
      maskscene: Scene,
      holescene: Scene,
      controls: OrbitControls,
      ended: false
    };
  },
  mounted() {
    let renderer = new WebGLRenderer();
    renderer.autoClear = false;
    renderer.setSize(600, 600);
    renderer.setClearColor(new Color(0.6, 0.6, 0.6), 1);

    let dom = renderer.domElement;
    document.getElementById("root").appendChild(dom);

    let camera = new PerspectiveCamera(75, 1, 0.1, 1000);
    // let camera = new OrthographicCamera(-100, 100, 100, -100, 0.1, 1000);
    camera.position.z = 100;
    let controls = new OrbitControls(camera, renderer.domElement)
    controls.enableKeys = false;

    let scene = new Scene();
    let maskscene = new Scene();
    let holescene = new Scene();

    let boxgeo = new BoxGeometry(100, 50, 1);
    let maskholegeo = new CylinderGeometry(10, 10, 1, 30, 1, false);
    let holegeo = new CylinderGeometry(10, 10, 1, 30, 1, true);

    let boxmat = new MeshPhongMaterial({
      color: new Color(0.8, 0, 0)
    });

    let holemat = boxmat.clone();
    holemat.side = BackSide;

    let light = new DirectionalLight(new Color(0.6, 0.6, 0.6));
    light.position.z = 100;
    let light2 = new DirectionalLight(new Color(0.4, 0.4, 0.4));
    light2.position.y = 100;
    light2.position.z = -100;

    let boxmesh = new Mesh(boxgeo, boxmat);
    let maskmesh = new Mesh(maskholegeo, holemat);
    maskmesh.rotateX(Math.PI*0.5);
    let holemesh = new Mesh(holegeo, holemat);
    holemesh.rotateX(Math.PI*0.5);

    scene.add(boxmesh);
    scene.add(light);
    scene.add(light2);

    maskscene.add(maskmesh);

    holescene.add(holemesh);
    holescene.add(light.clone());
    
    this.renderer = renderer;
    this.camera = camera;
    this.scene = scene;
    this.maskscene = maskscene;
    this.holescene = holescene;
    this.controls = controls;

    this.animate();
  },
  methods: {
    animate() {
      let renderer = <WebGLRenderer>this.renderer;
      let gl = renderer.domElement.getContext('webgl')!;
      renderer.clear();
      gl.enable(gl.STENCIL_TEST);
      gl.stencilFunc(gl.ALWAYS, 1, 1);
      gl.stencilOp(gl.KEEP, gl.KEEP, gl.REPLACE);
      renderer.render(this.maskscene, this.camera);

      renderer.clearColor();
      gl.stencilFunc(gl.EQUAL, 1, 1);
      gl.stencilOp(gl.KEEP, gl.KEEP, gl.KEEP);
      renderer.render(this.holescene, this.camera);

      gl.stencilFunc(gl.NOTEQUAL, 1, 1);
      renderer.render(this.scene, this.camera);

      this.controls.update();

      if(!this.ended){
       requestAnimationFrame(this.animate);
      }
    }
  },
  destroyed(){
    this.ended = true;
  }
});
