import {Aperture, RegionAperture} from './aperture';

export class Operation {
    _isclear = false;
    _name: string = "";
    _aperture: Aperture;
    constructor(aperture: Aperture, isclear: boolean) {
        this._aperture = aperture;
        this._isclear = isclear;
    }
}

export class LinearOperation extends Operation {
    _start: [number, number];
    _end: [number, number];
    constructor(aperture: Aperture, start: [number, number], end: [number, number], isclear: boolean) {
        super(aperture, isclear);
        this._name = "linear";
        this._start = start;
        this._end = end;
    }
}

export class ArcOperation extends Operation {
    _clockwise: boolean;
    _start: [number, number];
    _end: [number, number];
    _center: [number, number];
    constructor(aperture: Aperture, start: [number, number], end: [number, number], center: [number, number], clockwise: boolean, isclear: boolean) {
        super(aperture, isclear);
        this._name = "arc";
        this._start = start;
        this._end = end;
        this._center = center;
        this._clockwise = clockwise;
    }
}

export class TranslateOperation extends Operation {
    _position: [number, number];
    constructor(aperture: Aperture, position: [number, number], isclear: boolean) {
        super(aperture, isclear);
        this._name = "translate";
        this._position = position;
        this._aperture = aperture;
    }
}

export class RegionOperation extends Operation {
    get RegionAperture(): RegionAperture{
        return <RegionAperture>this._aperture;
    }
    constructor(aperture: Aperture, isclear: boolean) {
        super(aperture, isclear);
        this._name = "region";
    }
}