import { Operation, TranslateOperation, LinearOperation, RegionOperation } from './operation'
import { CircleAperture, RectangleAperture, ObroundAperture, RegularPolygonAperture } from './aperture';
import { isRegExp } from 'util';

function getRadToX(x: number, y: number, anti: boolean = false): number{
    if(x == 0 && y == 0){
        throw new Error("Parameter Error");
    }
    let rad = Math.acos(x/Math.sqrt(x*x+y*y));
    if(y < 0){
        rad = -rad;
    }
    if(anti == false){
        if(rad < 0){
            rad = -rad;
        }else{
            rad = Math.PI*2 - rad;
        }
    }
    return rad;
}
export class Graphic {
    _dom: HTMLCanvasElement;
    _operations: Operation[] = [];
    _ctx: CanvasRenderingContext2D;
    _transform: DOMMatrix = new DOMMatrix();
    _stop: boolean;
    get domElement() {
        return this._dom;
    }
    set operationSet(set: Operation[]) {
        this._operations = set;
    }
    stop(){
        this._stop = true;    
    }
    redraw = () => {
        let elt = this._dom;
        let ctx = this._ctx;
        ctx.resetTransform();
        ctx.clearRect(0, 0, elt.width, elt.height);
        ctx.fillStyle = '#333333';
        ctx.setTransform(this._transform);
        // ctx.fillRect(0,0,10,10);
        for (let op of this._operations) {
            if(op._isclear){
                ctx.globalCompositeOperation = 'destination-out';
            }else{
                ctx.globalCompositeOperation = 'source-over';
            }
            switch (op._name) {
                case 'translate':{
                    let translate = <TranslateOperation>op;
                    let aperture = op._aperture;
                    switch (aperture.Name) {
                        case 'C':{
                            let circle = <CircleAperture>aperture;
                            ctx.beginPath();
                            ctx.arc(translate._position[0], translate._position[1],
                                circle.Diameter * 0.5, 0, Math.PI * 2);
                            if (circle.HoleDiameter != 0) {
                                ctx.arc(translate._position[0], translate._position[1],
                                    circle.HoleDiameter*0.5, 0, Math.PI * 2, true);
                            }
                            ctx.closePath();
                            ctx.fill();
                            break;
                        }
                        case 'R':{
                            let rect = <RectangleAperture>aperture;
                            ctx.beginPath();
                            ctx.rect(-rect.Width*0.5+translate._position[0], 
                                -rect.Height*0.5+translate._position[1],
                                 rect.Width, rect.Height);
                            if (rect.HoleDiameter != 0) {
                                ctx.arc(translate._position[0], translate._position[1],
                                    rect.HoleDiameter*0.5, 0, Math.PI * 2, true);
                            }
                            ctx.closePath();
                            ctx.fill();
                            break;
                        }
                        case 'O':{
                            let obr = <ObroundAperture>aperture;
                            ctx.beginPath();
                            if(obr.Width == obr.Height){
                                ctx.arc(translate._position[0], translate._position[1],
                                    obr.Width*0.5, 0, Math.PI*2);
                            }else{
                                if(obr.Width < obr.Height){
                                    ctx.arc(translate._position[0], translate._position[1]+obr.Height*0.5,
                                        obr.Width*0.5, 0, Math.PI);
                                    ctx.arc(translate._position[0], -(translate._position[1]+obr.Height*0.5),
                                        obr.Width*0.5, Math.PI, Math.PI*2);
                                }else{
                                    ctx.arc(translate._position[0]+obr.Width*0.5, translate._position[1],
                                        obr.Height*0.5, -Math.PI*0.5, Math.PI*0.5);
                                    ctx.arc(-(translate._position[0]+obr.Width*0.5), translate._position[1],
                                        obr.Height*0.5, Math.PI*0.5, Math.PI*1.5);
                                }
                                ctx.closePath();
                            }
                            if (obr.HoleDiameter != 0) {
                                ctx.arc(translate._position[0], translate._position[1],
                                    obr.HoleDiameter*0.5, 0, Math.PI * 2, true);
                            }
                            ctx.closePath();
                            ctx.fill();
                            break;
                        }
                        case 'P':{
                            let rpoly = <RegularPolygonAperture>aperture;
                            let detal = Math.PI*2/rpoly.Count;
                            let start = rpoly.Rotation/180*Math.PI;
                            let sx = translate._position[0];
                            let sy = translate._position[1];
                            let r = rpoly.Diameter;
                            ctx.beginPath();
                            ctx.moveTo(sx+r*Math.cos(start), sy+r*Math.sin(start));
                            for(let i = 1; i < rpoly.Count; ++i){
                                ctx.lineTo(sx+r*Math.cos(start + detal*i), sy+r*Math.sin(start + detal*i));
                            }
                            if (rpoly.HoleDiameter != 0) {
                                ctx.arc(translate._position[0], translate._position[1],
                                    rpoly.HoleDiameter*0.5, 0, Math.PI * 2, true);
                            }
                            ctx.closePath();
                            ctx.fill();
                            break;
                        }
                        default:{
                            throw Error('Not Implement');
                        }
                    }
                    break;
                }
                case 'linear':{
                    let linear = <LinearOperation>op;
                    let aperture = linear._aperture;
                    switch(aperture.Name){
                        case 'C':{
                            let circle = <CircleAperture>aperture;
                            if(circle.HoleDiameter != 0){
                                throw Error('Not Implement');
                            }
                            if(linear._end[0] == linear._start[0] &&
                                linear._end[1] == linear._start[1]){
                                    ctx.beginPath();
                                    ctx.arc(linear._start[0], linear._start[1], circle.Diameter*0.5,
                                        0, 2*Math.PI, false);
                                    ctx.fill();
                            }else{
                                let rad = getRadToX(linear._end[0] - linear._start[0],
                                    linear._end[1] - linear._start[1], true) + Math.PI*0.5;
                                ctx.beginPath();
                                ctx.arc(linear._start[0], linear._start[1], circle.Diameter*0.5,
                                    rad, rad + Math.PI, false);
                                ctx.arc(linear._end[0], linear._end[1], circle.Diameter*0.5,
                                    rad + Math.PI, rad, false);
                                ctx.closePath();
                                ctx.fill();
                            }
                            break;
                        }
                        case 'R':{
                            let rect = <RectangleAperture>aperture;
                            if(rect.HoleDiameter != 0){
                                throw Error('Not Implement');
                            }
                            let currentsign: [number, number] = [0, 0];
                            currentsign = [Math.sign(linear._end[0]-linear._start[0]), 
                                Math.sign(linear._end[1]-linear._start[1])];
                            if(currentsign[0] == 0) currentsign[0] = 1;
                            if(currentsign[1] == 0) currentsign[1] = 1;
                            let changesign = (rsign: [number, number]):[number, number]=>{
                                return [-rsign[1], rsign[0]];
                            };
                            ctx.beginPath();
                            ctx.moveTo(linear._end[0] + currentsign[0]*rect.Width*0.5,
                                linear._end[1] + currentsign[1]*rect.Height*0.5);
                            currentsign = changesign(currentsign);
                            ctx.lineTo(linear._end[0] + currentsign[0]*rect.Width*0.5,
                                linear._end[1] + currentsign[1]*rect.Height*0.5);
                            ctx.lineTo(linear._start[0] + currentsign[0]*rect.Width*0.5,
                                linear._start[1] + currentsign[1]*rect.Height*0.5);
                            currentsign = changesign(currentsign);
                            ctx.lineTo(linear._start[0] + currentsign[0]*rect.Width*0.5,
                                linear._start[1] + currentsign[1]*rect.Height*0.5);
                            currentsign = changesign(currentsign);
                            ctx.lineTo(linear._start[0] + currentsign[0]*rect.Width*0.5,
                                linear._start[1] + currentsign[1]*rect.Height*0.5);
                            ctx.lineTo(linear._end[0] + currentsign[0]*rect.Width*0.5,
                                linear._end[1] + currentsign[1]*rect.Height*0.5);
                            ctx.closePath();
                            ctx.fill();
                            break;
                        }
                        default:{
                            throw new Error('Not Implement');
                        }
                    }
                    break;
                }
                case 'region':{
                    let region = <RegionOperation>op;
                    let aperture = region.RegionAperture;
                    ctx.beginPath();
                    ctx.moveTo(aperture.Points[0][0], aperture.Points[0][1]);
                    let i = 0;
                    for(let point of aperture.Points){
                        let x = point[0];
                        let y = point[1];
                        if(point[2] == true){
                            let prex = aperture.Points[i-1][0];
                            let prey = aperture.Points[i-1][1];
                            let nextx = aperture.Points[i+1][0];
                            let nexty = aperture.Points[i+1][1];
                            let rad = Math.sqrt(
                                Math.pow(prex - x, 2) +
                                Math.pow(prey - y ,2)
                            )
                            let sangle = getRadToX(prex - x, prey - y, true);
                            let eangle = getRadToX(nextx - x, nexty - y, true);
                            if(sangle == eangle){
                                ctx.arc(x, y, rad, 0, Math.PI*2, true);
                            }else{
                                ctx.arc(x, y, rad, sangle, eangle, point[3]);
                            }
                        }else{
                            ctx.lineTo(x, y);
                        }
                        ++i;
                    }
                    ctx.closePath();
                    ctx.fill();
                    // ctx.stroke();
                    break;
                }
                default:
                    // console.log(op._name);
            }
        }
        ctx.fill();

        if(!this._stop){
            requestAnimationFrame(this.redraw);
        }
    }
    constructor(name: string) {
        if(name == ""){
            this._dom = document.createElement('canvas');
        }else{
            this._dom = <HTMLCanvasElement>document.getElementById(name);
        }
        let elt = this._dom;
        this._ctx = elt.getContext('2d')!;
        let prex = 0;
        let prey = 0;

        elt.onmousedown = (ev: MouseEvent) => {
            prex = ev.x;
            prey = ev.y;
        }

        elt.onmousemove = (ev: MouseEvent) => {
            if (ev.buttons) {
                let mat = new DOMMatrix().translateSelf(ev.x - prex, ev.y - prey);
                this._transform = mat.multiply(this._transform);
                prex = ev.x; prey = ev.y;
            }
        }
        elt.onwheel = (ev: WheelEvent) => {
            let scale = Math.abs(ev.deltaY) / 1000 + 1;
            if (ev.deltaY > 0) {
                scale = 1 / scale;
            }
            let mat1 = new DOMMatrix().translate(-ev.offsetX, -ev.offsetY);
            let mat2 = new DOMMatrix().scale(scale, scale);
            let mat3 = new DOMMatrix().translate(ev.offsetX, ev.offsetY);
            this._transform = mat3.multiply(mat2.multiply(mat1.multiply(this._transform)));
            return false;
        }
        requestAnimationFrame(this.redraw);
    }
}