












import Vue from "vue";
import * as tf from "@tensorflow/tfjs";
import DigitInput from '../components/DigitInput.vue';

export default Vue.extend({
  data: () => {
    return {
      model: <tf.LayersModel | null>null,
      info: "",
      digit: ""
    };
  },
  async mounted() {
    this.info = "";
    console.log(require('file-loader!../assets/models/group1-shard1of1.bin'));
    let modelurl = location.origin + <string>require('../assets/models/model.json')
    let model = await tf.loadLayersModel(modelurl);
    this.model = model;
    model.summary(undefined, undefined, msg => {
       this.info = this.info + msg + "\n";
    });
  },
  components:{
    DigitInput
  }
})
